*{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {
  height: 100%;
  margin: 0;
  font-family: 'Arial', sans-serif;
  background: #E5E5E5; /* Similar to light gray background in the image */
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-container {
  width: 100%;
  max-width: 800px; /* Adjust the width as per the design */
  margin: auto;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  background: #ffffff;
}

.login-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  padding: 40px;
}

.title-section {
  border-right: 2px solid #D3D3D3;
  padding-right: 40px;
}

.title-section h1 {
  color: #333;
  margin-bottom: 0.5em;
}

.title-section p {
  /* color: #f47409; */
  color: black;
  font-size: 14px;
}

.form-section {
  padding-left: 40px;
}

.login-title {
  font-size: 24px;
  color: #f47409;
  margin-bottom: 2em;
  text-align: center;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.login-form input {
  padding: 15px;
  margin-bottom: 15px;
  border: 1px solid #D3D3D3;
  border-radius: 4px;
  font-size: 14px;
}

.form-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.remember-me {
  display: flex;
  align-items: center;
}

.remember-me input {
  margin-right: 5px;
}

.forgot-password {
  color: #4A5568;
  text-decoration: none;
}

.login-button {
  padding: 15px;
  background-color: #f47409;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.login-button:hover {
  background-color: #2D3748;
}

.footer-section {
  grid-column: span 2;
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  border-top: 2px solid #D3D3D3;
  margin-top: 20px;
}

.footer-link {
  color: #4A5568;
  text-decoration: none;
  font-size: 14px;
}

.footer-link:hover {
  text-decoration: underline;
}
